import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input, message, Select, Spin } from 'antd';
import { getPaperType, createPaperType, updatePaperType } from 'services/PaperType';
import { getQueryParams } from 'utils/urlUtil';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
    typeId: 1,
  };
  return { ...initialValueDefault, ...initialValue };
};

const { Option } = Select;

@Form.create({ name: 'formPaperType' })
class FormPaperType extends Component {
  state = {
    systemId: false,
    initialValue: {
      id: '',
      label: '',
      typeId: 1,
    },
    loading: false,
    disableCode: false,
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();

    if (queryParamId) {
      this.setState({
        disableCode: true,
      });
    }

    if (queryParamId) {
      this.setState({ systemId: queryParamId, loading: true, queryParamId });
      getPaperType(queryParamId)
        .then(res => {
          const initialValue = handleInitialValue(res.data);
          this.setState({ initialValue, loading: false });
        })
        .catch(error => {
          console.log('error', error);
          this.setState({ loading: false });
        });
    }
  };

  handleSubmit = e => {
    const { queryParamId } = this.state;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (queryParamId) {
          updatePaperType(queryParamId, values)
            .then(() => {
              message.success('Chỉnh sửa giấy tờ thành công');
              this.props.history.push(routeConstant.DANH_MUC__TUYEN_SINH__LOAI_GIAY_TO);
            })
            .catch(error => {
              console.log('res', error);
              message.error(error.response.data.message);
            });
        } else {
          createPaperType(values)
            .then(() => {
              message.success('Thêm giấy tờ thành công');
              this.props.history.push(routeConstant.DANH_MUC__TUYEN_SINH__LOAI_GIAY_TO);
            })
            .catch(error => {
              console.log('res', error.response);
              message.error(error.response.data.message);
            });
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { systemId } = this.state;
    return (
      <Card title={systemId ? 'Cập nhật giấy tờ' : 'Thêm loại giấy tờ'}>
        <Spin spinning={this.state.loading}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              <Form.Item hasFeedback label="Ký hiệu" {...formItemLayout}>
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input disabled={this.state.disableCode} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Tên giấy tờ" hasFeedback>
                {getFieldDecorator('label', {
                  initialValue: initialValue.label,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Loại giấy tờ">
                {getFieldDecorator('typeId', {
                  initialValue: initialValue.typeId,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(
                  <Select>
                    <Option value={0} key={0}>Nhập học</Option>
                    <Option value={1} key={1}>Giấy tờ thường</Option>
                  </Select>
                )}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormPaperType {...props} />
  </Page>
));



